import axios from "axios";
import { instance } from "./instance";

export const getManifest = async (roverName: string) => {
  return instance.request<Manifest>({
    url: `https://mars-photos.herokuapp.com/api/v1/manifests/${roverName}`,
    transformResponse: (r) => r.photo_manifest
  });

  // let apiData = await instance.get(
  //   `https://mars-photos.herokuapp.com/api/v1/manifests/${roverName}`
  // );
  // apiData = apiData.data.photo_manifest;
  // if (!apiData) throw new Error("Данных нет");
  // return apiData;
};

export interface Manifest {
  name: string;
  landing_date: Date | undefined;
  launch_date: Date | undefined;
  status: string;
  max_sol: number;
  max_date: Date | undefined;
  total_photos: number;
  photos: Array<ManifestPhoto>;
}

export interface ManifestPhoto {
  sol: number;
  earth_date: Date | undefined;
  total_photos: number;
  cameras: Array<string>;
}
